import axios from 'axios';
import localForage from "localforage";
import { values } from './default.js';
var CACHE_PREFIX = "pokeapi-js-wrapper-";

function loadResource(url) {
  return new Promise(function (resolve, reject) {
    localForage.ready().then(function () {
      localForage.getItem("".concat(CACHE_PREFIX).concat(url)).then(function (value) {
        if (value === null) {
          loadUrl(url).then(function (res) {
            resolve(res);
          }).catch(function (err) {
            reject(err);
          });
        } else {
          resolve(addCacheMark(value));
        }
      }).catch(function (error) {
        loadUrl(url).then(function (res) {
          resolve(res);
        }).catch(function (err) {
          reject(err);
        });
      });
    }).catch(function (err) {
      loadUrl(url).then(function (res) {
        resolve(res);
      }).catch(function (err) {
        reject(err);
      });
    });
  });
}

;

function loadUrl(url) {
  return new Promise(function (resolve, reject) {
    var options = {
      baseURL: "".concat(values.protocol, "://").concat(values.hostName, "/"),
      timeout: values.timeout
    };
    axios.get(url, options).then(function (response) {
      // if there was an error
      if (response.status >= 400) {
        reject(response);
      } else {
        // if everything was good
        // cache the object in browser memory
        // only if cache is true
        if (values.cache) {
          localForage.setItem("".concat(CACHE_PREFIX).concat(url), response.data);
        }

        resolve(addCacheMark(response.data, 0));
      }
    }).catch(function (err) {
      reject(err);
    });
  });
}

function addCacheMark(object) {
  var cached = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : 1;
  //object.fromCache = cached;
  return object;
}

export { loadResource };